import React, { useState, useEffect } from 'react'
import { Menu } from 'antd'
import Axios from 'axios'
import { useSelector } from 'react-redux'
const SubMenu = Menu.SubMenu

function LeftMenu(props) {
    
    const user = useSelector(state => state.user)

        const MenuLogin = () => {
            return (
                <Menu mode={props.mode}>
                    <Menu.Item key="mail">
                        <a href="/notice" style={{ fontWeight: 'bold' }}></a>
                    </Menu.Item>
                    {user.userData && user.userData.isAdmin ?
                    <SubMenu title={<span></span>} style={{ fontWeight: 'bold' }}>
                        <Menu.Item key="mail">
                            <a href="/upload"></a>
                        </Menu.Item>
                        <Menu.Item key="mail">
                            <a href="/uploadEnt"> </a>
                        </Menu.Item>
                        <Menu.Item key="mail">
                            <a href="/uploadArtist"> </a>
                        </Menu.Item>
                    </SubMenu>
                    :
                    <SubMenu title={<span></span>}>
                        <Menu.Item key="mail">
                            <a href="/upload"></a>
                        </Menu.Item>
                    </SubMenu>
                    }
                </Menu>
            )
        }

        const MenuNotLogin = () => {
            return (
                <Menu mode={props.mode}>
                    <Menu.Item key="mail">
                        <a href="/notice" style={{ fontWeight: 'bold' }}></a>
                    </Menu.Item>
                </Menu>
            )
        }


        if (user.userData && user.userData.isAuth) {
            return (
                <MenuLogin />
            )
        } else {
            return (
                <MenuNotLogin />
            )
        }
}

export default LeftMenu
